.block-products {
  color: #fff;
  &__nav {
    margin-bottom: 60px;
    font-size: 16px;
    @media (min-width: 410px) {
      font-size: 20px;
    }
    @media (min-width: 576px) {
      font-size: 24px;
    }
    @media (min-width: 768px) {
      font-size: 36px;
    }
    .splide__pagination, .splide__arrows {
      display: none !important;
    }
    .splide__track {
      overflow: visible !important;
      .splide__list {
        align-items: center;
        height: auto;
        .splide__slide {
          margin-right: auto;
          &:last-child {
            margin-right: 0;
          }
        }
      }
    }
    &__btn {
      appearance: none;
      background: none;
      border: 0;
      border-radius: 0;
      font-family: ToxigenesisRg-Bold,sans-serif;
      position: relative;
      line-height: 1;
      padding: 0 (24em/36) (12em/36);
      color: #fff;
      white-space: nowrap;
      transition: color 0.3s;
      &:hover {
        color: #ffe900;
      }
      &:after {
        content: '';
        position: absolute;
        width: calc(100% - #{48em/36});
        height: (7em/36);
        background: #ffe900;
        transition: transform 0.3s;
        left: (24em/36);
        bottom: 0;
        transform: scaleX(0);
        transform-origin: 100% 0%;
      }
      &.js-active:after {
        transform-origin: 0% 0%;
        transform: scaleX(1);
      }
    }
  }
  &__items {
    .splide__pagination, .splide__arrows {
      display: none !important;
    }
    .splide__track {
      .splide__list {
        height: auto;
        .splide__slide {
          background-repeat: no-repeat;
          background-position: top center;
          background-size: 100%;
          @media (max-width: 991.98px) {
            background-image: none !important;
          }
        }
      }
    }
    .row {
      display: flex;
      align-items: center;
      flex-wrap: wrap;
      max-width: 615px;
      @media (min-width: 992px) {
        flex-wrap: nowrap;
        max-width: 1200px;
      }
    }
    &__img {
      flex: 0 0 auto;
      width: 100%;
      margin-bottom: 20px;
      @media (min-width: 992px) {
        width: 50%;
        margin-right: 25px;
        margin-bottom: 0;
      }
    }
    &__desc {
      flex: 1 1 auto;
    }
  }
}
body .hero-product {
  @media (max-width: 767.98px) {
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 50px;
  }
}
body .hero .row {
  @media (max-width: 767.98px) {
    justify-content: flex-end;
  }
}
body .hero-product img {
  @media (max-width: 767.98px) {
    margin-left: -0% !important;
    margin-top: 0 !important;
    width: 130% !important;
    max-width: 480px !important;
    margin-bottom: 10%;
    flex: 0 0 auto;
  }
}